<template>
  <div class="body-bg">
    <div class="login-h">账号密码登录</div>
    <!-- <router-link to="/GysZcXzjs">
      <div class="login-h-mini">还没有账号，立即<a>注册</a></div>
    </router-link> -->
    <div class="login-form">
      <van-cell-group>
        <div class="input-item relative">
          <van-field
            ref="name"
            v-model="users.userAccount"
            v-validate="{'required': 'true'}"
            :error-message="errors.first('userAccount')"
            clearable
            name="userAccount"
            placeholder="请输入登录账号"
            @focus="showSrls = true"
            @blur="showSrls = false"
          />

          <!-- <div :class="(showSrls && srlsList && srlsList.length > 0 ) ?'input-dropdown-item show':'input-dropdown-item' ">
            <div v-for="(item,index) of srlsList" :key="index" class="account-record">
              <div class="record-item" @click="xzUsername(item)">
                <dl>
                  <dt>{{ item.username }}</dt>
                  <dd>******</dd>
                </dl>
                <div class="btn"><i class="iconfont iconqingchu" /></div>
              </div>
            </div>
          </div> -->
        </div>

        <div class="input-item password">
          <van-field
            ref="id"
            v-model="users.passWord"
            v-validate="{'required': 'true'}"
            clearable
            :error-message="errors.first('password')"
            :type="eyesChange ==true?'text':'password'"
            name="password"
            placeholder="请输入登录密码"
          />

          <!-- 显示与隐藏密码 -->
          <div v-if="eyesChange ==true" class="input-operator showpassword" @click="showEye()">
            <i class="iconfont iconkejian" />
          </div>
          <div v-else class="input-operator showpassword" @click="showEye()">
            <i class="iconfont iconbukejian" />
          </div>
        </div>
      </van-cell-group>
      <!-- 登录按钮 begin -->
      <div class="login-button">
        <van-button round type="info" @click="handleSubmit">登 录</van-button>
      </div>
      <!-- 登录按钮 end -->

      <div class="agreement-item flex">
        <van-checkbox v-model="radio" :disabled="disabled" @click="isRead()" />
        <div class="text">我已认真阅读并同意 <span class="blue" @click="goGysZcxy()">《服务协议》</span></div>
      </div>
      <p v-if="fwxyts" class="input-item-msg">请阅读并同意服务及隐私协议</p>

      <!-- <div class="login-worktime">
        <div class="worktime-tit">网站工作时间</div>
        <div class="worktime-time">上午：9:00-12:00  下午：14:00-17:00</div>
      </div> -->
    </div>
  </div>

</template>

<script land="script" scoped>
import { NativeJS } from '@/common/js/mixin.js'
import { getUserName, getPass } from '../../../common/js/catch'
import { mhLogin } from '@/store/module.js'
import { setToken, setUserName, setPass } from '@/common/js/catch'

export default {
  title: 'login',
  components: {
  },
  mixins: [NativeJS],
  data() {
    return {
      srlsList: [],
      showSrls: false,
      fwxyts: false,
      disabled: false, // 是否阅读服务协议
      radio: false, // 是否选中服务协议
      profiletitle: '',
      /* 未登录弹出框*/
      status: {
        isShowPublish: false,
        topNum: 20
      },
      isEmpty: true, // 判断按钮是否可以点击
      loading: false,
      eyesChange: false, // 是否展示密码
      users: {
        userAccount: '',
        passWord: ''
      },
      captchaCodeImg: null, // 验证码地址
      codeNumber: null, // 验证码
      showAlert: false, // 显示提示组件
      alertText: null, // 提示的内容
      phoneType: '' // 机型  android  ios
    }
  },
  created() {
    this.srlsList = window.localStorage.getItem('srlsList') ? JSON.parse(window.localStorage.getItem('srlsList')) : []
    this.disabled = this.$store.state.mh.isRead
    this.radio = this.$store.state.mh.radio
    this.users.userAccount = this.$store.state.mh.username
    this.users.passWord = this.$store.state.mh.password
    if (this.$store.state.mh.username) {
      this.users.userAccount = this.$store.state.mh.username
      this.users.passWord = this.$store.state.mh.password
    } else {
      if (this.srlsList && this.srlsList.length > 0) {
        this.users.userAccount = this.srlsList[this.srlsList.length - 1].username
        this.users.passWord = this.srlsList[this.srlsList.length - 1].password
      }
    }
    if (this.users.userAccount != null && this.users.passWord != null) {
      this.isEmpty = false
    }
    // this.getFiles()
    var self = this
    // 二维码返回数据
    window['LoginSuccess'] = function(data) {
      window.localStorage.setItem('isFirst', JSON.stringify(1))
      console.info('LoginSuccess======', data)
      setToken(data.token) // 保存token
      setUserName(self.users.userAccount) // 保存登录名称
      setPass(self.users.passWord) // 保存密码
      self.$store.commit('mh/SET_MHUSER_INFO', data)
      self.$store.commit('mh/SET_IS_MHLOGIN', true)
      self.$toast.success('登录成功！')
      self.$router.push({ name: 'MhHome' })
    }
    this.getPhoneType()
  },
  methods: {
    getPhoneType() {
      const _this = this
      if (process.env.NODE_ENV !== 'development') {
        try {
          _this.phoneType = window.myapp.getPhoneType()
          _this.goJcgx()
        } catch (err) {
          console.log(err)
        }
        this.$bridge.callhandler('getPhoneType', '', (data) => {
          _this.phoneType = data
          _this.goJcgx()
        })
      } else {
        this.$toast('返回原生页面')
      }
    },
    xzUsername(item) {
      this.users.userAccount = item.username
      this.users.passWord = item.password
    },
    goJcgx() {
      let str = ''
      if (this.phoneType === 'android') {
        str = 'erp/queryAndroidLatestVersionAction'
      } else {
        str = 'erp/queryIosLatestVersionAction'
      }
      this.$store.dispatch(str).then(res => {
        console.info('pinpinpinpinpinpinpin===1===', res)
        if (res.data) {
          const pin = { 'Bbh': res.data.bbh,
            'flag': 'sy',
            'sfqzgx': res.data.sfqzgx,
            'gxnr': res.data.bbms,
            'xzdz': res.data.qlwjurl
          }
          console.info('pinpinpinpinpinpinpin===2===', res)
          if (process.env.NODE_ENV !== 'development') {
            try {
              console.info('NativeGetAllCAInfo======')
              window.myapp.checkUpdate(JSON.stringify(pin))
            } catch (err) {
              console.log(err)
            }
            this.$bridge.callhandler('checkUpdate', JSON.stringify(pin), (data) => {
            })
          }
        }
      })
    },
    goGysZcxy() {
      this.$store.commit('mh/SET_NAME', this.users.userAccount)
      this.$store.commit('mh/SET_PASS', this.users.passWord)
      this.disabled = false
      this.$router.push({
        name: 'loginXy'
      })
    },
    isRead() {
      if (this.disabled === true) {
        this.$toast('请先阅读服务协议！')
      } else {
        if (this.radio) {
          window.localStorage.setItem('loginXyState', true)
        } else {
          window.localStorage.setItem('loginXyState', false)
        }
        return
      }
    },
    checkData() {
      if (this.users.userAccount && this.users.passWord) {
        this.isEmpty = false
      } else {
        this.isEmpty = true
      }
    },
    showEye() {
      this.eyesChange = !this.eyesChange
    },
    // handleZC() {
    //   this.$router.push('/gyszc')
    // },
    handleSubmit() {
      if (!this.radio) {
        this.$toast('请先阅读服务协议！')
        return
      }

      this.$validator.validate().then(result => {
        if (result) {
          this.$store.dispatch('mh/loginMh', {
            username: this.users.userAccount,
            password: this.users.passWord
          }).then(res => {
            window.localStorage.setItem('isFirst', JSON.stringify(1))
            this.NativeLoginAction(this.users.userAccount)
            this.$toast.success('登录成功！')
            this.$store.commit('mh/SET_NAME', '')
            this.$store.commit('mh/SET_PASS', '')
            setToken(res.token) // 保存token
            setUserName(this.users.userAccount) // 保存登录名称
            setPass(this.users.passWord) // 保存密码
            this.$store.commit('mh/SET_MHUSER_INFO', res)
            if (this.srlsList.length === 0) {
              this.srlsList.push({
                username: this.users.userAccount,
                password: this.users.passWord
              })
            } else {
              for (let i = 0; i < this.srlsList.length; i++) {
                if (this.srlsList[i].username === this.users.userAccount) {
                  if (this.srlsList[i].password === this.users.passWord) {
                    break
                  } else {
                    this.srlsList[i].password = this.users.passWord
                  }
                } else {
                  this.srlsList.push({
                    username: this.users.userAccount,
                    password: this.users.passWord
                  })
                }
              }
            }
            console.log('srlsList========================================================', this.srlsList)
            window.localStorage.setItem('srlsList', JSON.stringify(this.srlsList))
            this.$store.commit('mh/SET_RADIO', true)
            this.$store.commit('mh/SET_IS_MHLOGIN', true)
            this.$router.push({ name: 'MhHome' })
          }).catch((error) => {
            this.$toast(error)
          })
        }
      })
    }
  }
}

</script>

<style lang="css" scoped>

</style>
